<template>
    <div class="common-wrap">
        <div class="common-left" :style="{width: (100 - otherVariable.contentWidth) + '%'}" v-show="!otherVariable.isSpread">
            <!---->
            <div class="common-btn-wrap">
                <el-button @click="handleRefresh"></el-button>
                <el-button @click="handleReset"></el-button>
            </div>
            <!---->
            <CommonSearch
                :isShowStatus="isShowStatus"
                :propData="searchProp"
                :searchForm="searchForm"
                @selectChange="selectChange"
                @getData="getSearchList"
            >
            </CommonSearch>
            <!---->
            <CommonTable
                v-if="isResize"
                ref="commontable"
                :mainHeight="mainHeight"
                :isShowStatus="isShowStatus"
                :propData="tableProp"
                @getInitPage="getInitPage"
                :otherVariable="otherVariable"
                :tableData="tableData"
                @handleDetail="handleDetail"
            >
            </CommonTable>
            <!---->
            <CommonPagination
                :pageObj="pageObj"
                @getData="getData"
            >
            </CommonPagination>
        </div>
        <div class="common-right" :style="{width: otherVariable.contentWidth + '%'}">
            <!--/-->
            <CommonDetailX
                :otherVariable="otherVariable"
                :propData="formProp"
                :detailForm="detailForm"
                @toggleSpread="toggleSpread"
            >
                <!---->
                <template slot="header">
                    <div class="header-form" ref="headForm">
                        <template v-for="item in formProp">
                            <div v-if="item.type === 'outter'">
                                <span>{{ item.name ? (item.name + ':') : '' }}</span>
                                <span>{{ detailForm[item.prop]===''?'':(detailForm[item.prop])==''?'':detailForm[item.prop] }}</span>
                            </div>
                        </template>
                    </div>
                </template>
                <!---->
                <template slot="main-btn-wrap" v-if="!otherVariable.isChildEdit">
                    <div v-if="docStatus === 50" class="xs-audit-icon" @click="!otherVariable.isSaving && handleAdudit(100)" :class="otherVariable.isSaving ? 'banClick' : ''" ></div>
                    <div v-if="docStatus === 100" class="xs-audit-icon" @click="!otherVariable.isSaving && handleAdudit(50)" :class="otherVariable.isSaving ? 'banClick' : ''" ></div>
                </template>
                <!---->
                <template slot="head-form-title">
                    <div class="head-form-title">
                        <span>&nbsp;{{ detailForm.projectName }}{{ detailForm.projectCode?("("+ detailForm.projectCode + ")"):"" }}</span>
                        <span></span>
                    </div>
                </template>
                <!---->
                <template slot="head-form-content">
                    <div class="head-form-content">
                        <template v-for="item in formProp">
                            <div v-if="item.type === 'inner' && !item.isLong">
                                <span>{{ item.name }}</span>
                                <span>{{ detailForm[item.prop] }}</span>
                            </div>
                        </template>
                    </div>
                    <div class="remark-wrap">
                        <div></div>
                        <div class="headspan scrollBar" :title="detailForm.remark">{{ detailForm.remark }}</div>
                    </div>
                </template>
                <!---->
                <template slot="common-content">
                    <!---->
                    <div class="tab-btn-wrap" :style="{left: tabLeft}"></div>
                    <!--tab-->
                    <el-tabs v-model="activeName" class="common-content-tab order_style">
                        <el-tab-pane label="" name="first">
                            <!--:span-method="objectSpanMethod"-->
                            <el-table
                                v-if="isResize"
                                class="childtable"
                                ref="childTable"
                                tooltip-effect="dark"
                                v-loading="loadingI"
                                :data="tabDataI"
                                border
                                :height="tableHeight"
                                highlight-current-row
                            >
                                <el-table-column label="" align="left" width="130">
                                    <template slot-scope="scope">
                                        <span>{{ pricetypeList[(scope.row.priceType-1)] }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="">
                                    <el-table-column label="" prop="itemCode" align="left" width="80" :show-overflow-tooltip="true"></el-table-column>
                                    <el-table-column label="" prop="itemName" align="left" width="130" :show-overflow-tooltip="true"></el-table-column>
                                </el-table-column>
                                <el-table-column label="" prop="itemTypeName" align="left" width="120"></el-table-column>
                                <el-table-column label="" prop="cx2Name" align="left" width="110"></el-table-column>
                                <el-table-column label="" prop="cx6Name" align="left" width="110"></el-table-column>
                                <el-table-column label="()" prop="price" align="right" width="70"></el-table-column>
                                <el-table-column label="" prop="uom" align="left" width="60"></el-table-column>
                                <el-table-column label="" prop="currency" align="left" width="60"></el-table-column>
                                <el-table-column label="" prop="remark" align="right" :show-overflow-tooltip="true"></el-table-column>
                            </el-table>
                        </el-tab-pane>
                    </el-tabs>
                </template>
            </CommonDetailX>
        </div>
    </div>
</template>

<script>
    import CommonMixins from '$src/components/CommonMixins'
    import CommonSearch from '$src/components/CommonSearch'
    import CommonTable from '$src/components/CommonTable'
    import CommonPagination from '$src/components/CommonPagination'
    import CommonDetailX from '$src/components/CommonDetailX'
    import SelectEnter from '$src/components/SelectEnter'
    export default {
        mixins: [CommonMixins],
        components: {
            CommonSearch,
            CommonTable,
            CommonPagination,
            CommonDetailX,
            SelectEnter,
        },
        data() {
            return {
                tableHeight: this.$store.state.screenHei*0.67,
                mainHeight: 716,
                notAllowUpHeight: 0,
                notAllowDownHeight: 0,
                scrollHeight: 0,
                //
                isResize: true,
                isBanOperate: true,
                isShowCopyBtn: false,
                operateType: 'add',
                topFIds: [],
                isCanSave: false,
                tempWidth: null,
                isShowStatus: {
                    isMore: false,
                    isRead: true,
                    isPopUp: false
                },
                //
                otherVariable: {
                    isSpread: false,
                    contentWidth: 70,
                    detailTitle: '',
                    addTitle: '',
                    popupTitle: '',
                    isInlineForm: true,
                    isEdit: false,
                    isChildEdit: false,
                    isHeadComplex: true,
                    isFormComplex: true,
                    loading: false,
                    $isCustom: true,
                    isSaving: false
                },
                //
                searchProp: [
                    {
                        name:'',
                        prop:'docCode',
                        type:'normal',
                        isMainSearch:true,
                        isMain:false,
                        placeholder:''
                    },
                    {
                        name:'',
                        prop:'tempStatus',
                        type:'selectChange',
                        selectList:[{value:1,label:''},{value:2,label:''},{value:3,label:''}],
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'docDate',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'clientCode',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'clientCode2',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                ],
                //
                formProp: [
                    {name:'',prop:'docCode',type:'outter'},
                    {name:'',prop:'docDate',type:'outter'},
                    {name:'',prop:'clientCode',type:'inner'},
                    {name:'',prop:'offerType',type:'inner'},
                    {name:'',prop:'pricingModelName',type:'inner'},
                    {name:'',prop:'clientCode2',type:'inner'},
                    {name:'',prop:'regionName',type:'inner'},
                    //todo
                    {name:'',prop:'',type:'inner'},
                    {name:'',prop:'remark',type:'inner',isLong:true}
                ],
                //
                tableProp: [
                    {
                        name:'fid',
                        prop:'fid',
                        type:'normal',
                        isMain:true
                    },
                    {
                        name1:'/',
                        prop1:'docCode',
                        name2:'',
                        prop2:'docStatusName',
                        type:'mixinNormal',
                        width: '115'
                    },
                    {
                        name1:'/',
                        prop1:'offerType',
                        name2:'',
                        prop2:'docDate',
                        type:'mixin',
                        width: '130'
                    },
                    {
                        name1:'/',
                        prop1:'clientCode',
                        name2:'',
                        prop2:'regionName',
                        type:'mixin',
                        width: '120'
                    },
                    {
                        name1:'/',
                        prop1:'enterName',
                        name2:'',
                        prop2:'enterTime',
                        type:'mixin',
                        width: '148'
                    }
                ],
                tableData: [],
                searchForm: {tempStatus: 2,minDocStatus: 50, maxDocStatus: 100},
                addOrEditForm: {},
                addForm: {},
                detailForm: {},
                copyForm: {},
                tempForm: {},
                submitRow: {},
                detailId: null,
                docStatus: null,
                //
                pageObj: {
                    commonPage: 1,
                    commonSize: 10,
                    commonSizes: [10, 20, 30, 40, 50, 100],
                    commonTotal: 0
                },
                //
                tabLeft: '100px',
                activeName: 'first',
                childDetailRow: {},
                copyChildDetailRow: {},
                clickTimes: 0,
                isAddDetail: false,
                //
                tabDataI: [],
                copyDataI: [],
                auditData: [],
                spanArr: [],
                position: 0,
                loadingI: false,
                pricetypeList: ['','','','',''],
                isShowNotAllowed: false,
                showButton:false
            }
        },
        watch: {
            'isShowStatus.isPopUp':{
                handler(val) {
                    this.isShowNotAllowed = val;
                }
            },
            'otherVariable.isChildEdit':{
                handler(val) {
                    this.isShowNotAllowed = val;
                }
            },
            deep: true
        },
        mounted() {
            // 
            this.getData();
            this.rowspan();
            // /
            this.listenScroll();
            this.listenResize();
            this.changeSize();
            this.tempWidth = this.otherVariable.contentWidth;
        },
        methods: {
            //
            getData(isFirst = true) {
                this.tableData = [];
                this.fetch(`/oms/sls/OfferPrice/getOfferPriceMasterList?pageNo=${this.pageObj.commonPage}&pageSize=${this.pageObj.commonSize}`,this.searchForm)
                    .then(result => {
                        if(result.data && result.data.list && result.data.list.length) {
                            this.tableData = result.data.list;
                            this.pageObj.commonTotal = result.data.total;
                            if(isFirst) {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.tableData[0].fid);
                                })
                            }else {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.detailId);
                                })
                            }
                        }else {
                            this.tabDataI = [];
                            this.copyDataI = [];
                            this.detailForm = {};
                            this.detailId = null;
                            this.docStatus = null;
                            this.pageObj.commonTotal = 0;
                        }
                    })
            },
            selectChange(val) {
                if(val === 1) {
                    this.searchForm.minDocStatus = 50;
                    this.searchForm.maxDocStatus = 101;
                }else if(val === 2) {
                    this.searchForm.minDocStatus = 50;
                    this.searchForm.maxDocStatus = 100;
                }else if(val === 3) {
                    this.searchForm.minDocStatus = 100;
                    this.searchForm.maxDocStatus = 101;
                }
            },
            getSearchList(){
                this.pageObj.commonPage=1;
                this.pageObj.commonSize=10;
                this.getData();
            },
            //
            handleRefresh() {
                this.isBanOperate = true;
                this.getData();
                this.detailForm = {};
                this.detailId = null;
                this.docStatus = null;
                this.tabDataI = [];
            },
            //
            handleReset() {
                this.searchForm = {tempStatus: 2,minDocStatus: 50, maxDocStatus: 100};
                this.handleRefresh();
            },
            getInitPage() {
                this.pageObj.commonPage = 1;
                this.pageObj.commonSize = 10;
                this.getData();
            },
            //
            handleDetail(row) {
                row.docStatus >= 50 ? this.showButton = true : this.showButton = false;
                this.tabDataI = [];
                this.copyDataI = [];
                this.isShowStatus.isRead = true;
                this.detailForm = {};
                this.fetch('/oms/sls/OfferPrice/getOfferPriceMasterList',{fid:row.fid})
                    .then(result => {
                        if(result.status === 200) {
                            this.detailForm = result.data.list[0];
                            this.copyForm = this.$lodash.cloneDeep(result.data.list[0]);
                        }else {
                            this.$message({
                                message: '',
                                type: 'warning'
                            });
                            this.detailForm = {};
                            this.copyForm = {};
                        }
                    });
                this.tabDataI = this.giveState(this.tabDataI);
                this.getChildTable(row.fid);
                this.detailId = row.fid;
                this.docStatus = row.docStatus;
            },
            //
            handleAdudit(status){
                if(this.detailForm.fid) {
                    this.auditData = [];
                    this.tabDataI.forEach(item => {
                        this.auditData.push({fId:item.fid,price:item.price,priceType:item.priceType,itemCode:item.itemCode})
                    });
                    this.$confirm('?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.otherVariable.isSaving = true;
                        this.fetch(`/oms/sls/OfferPrice/auditingPrice`,{masterId:this.detailForm.fid,regionId:this.detailForm.regionId,offerPriceDetailList:this.auditData,docStatus:status})
                            .then(result => {
                                if(result.status === 200) {
                                    this.$message({
                                        message:result.message,
                                        type:'success'
                                    });
                                    this.getData();
                                }else {
                                    this.$message({
                                        message:this.returnErrorMsg(result.error,'second'),
                                        type:'error'
                                    })
                                }
                            })
                            .then(() => {
                                this.otherVariable.isSaving = false;
                            })
                    }).catch(() => {
                        console.log('cnacel')
                    })

                }else {
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                }
            },
            //
            getChildTable(fid) {
                this.clickTimes = 0;
                this.tabDataI = [];
                this.loadingI = true;
                this.isShowNotAllowed = true;
                this.getAjax(`/oms/sls/OfferPrice/getOfferPriceDetailListByMasterId?pageSize=1000&masterId=${fid}`)
                    .then(result => {
                        this.loadingI = false;
                        this.isShowNotAllowed = false;
                        if(result.data && result.data.list) {
                            this.tabDataI = result.data.list;
                            this.copyDataI = this.$lodash.cloneDeep(this.tabDataI);
                        }
                    })
                    .then(() => {
                        this.loadingI = false;
                    })
            },
            //
            rowspan() {
                this.tabDataI.forEach((item,index) => {
                    if(index === 0) {
                        this.spanArr.push(1);
                        this.position = 0;
                    }else {
                        if(this.tabDataI[index].priceType === this.tabDataI[index-1].priceType ) {
                            this.spanArr[this.position] += 1;
                            this.spanArr.push(0);
                        }else {
                            this.spanArr.push(1);
                            this.position = index;
                        }
                    }
                })
            },
            objectSpanMethod({ row, column, rowIndex, columnIndex }) {  //
                if (columnIndex === 0) {
                    const _row = this.spanArr[rowIndex];
                    const _col = _row>0 ? 1 : 0;
                    return {
                        rowspan: _row,
                        colspan: _col
                    }
                }
            },
        }
    }
</script>

<style lang="less" scoped>
    .popup-enter-active, .popup-leave-active {
        transition: left 0.1s;
        left: 0;
    }
    .popup-enter, .popup-leave-to {
        left: 100%;
    }
</style>



